import React from "react";
import { H1, H2 } from "../../components/Header";
import MainSectionWrapper from "../../index";
import StripeBuyButton from "../../components/StripeBuyButton";
import EtsyLink from "../../components/EtsyLink";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import JsonLd from "../../jsonLd";

import "./styles.scss";

const baseLdJson = {
    "@context": "https://www.schema.org",
    "@type": "product",
    "brand": "Crochet Nest",
    "logo": "https://crochetnest.com/icons/icon-512x512.png",
    "aggregateRating": {
        "@type": "aggregateRating",
        "ratingValue": "5",
        "reviewCount": "24"
    }
};

const ShippingDisclaimer = () => {
    return (
        <div>
            <p><b>Made to order, will ship in 1 - 2 weeks.</b></p>
            <p>Note: Each item is made with highest care and attention to details. However due to the nature of handmade items, slight differences may show from the pictures.</p>
            <p><i>Gentle use is recommended</i></p>
        </div>
    );
}

const ProductDetailsImages = (props) => {
    const { coverPhoto, name } = props;

    const images = [
        coverPhoto,
        coverPhoto,
        coverPhoto,
    ];

    const imagesToShow = images.slice(0, 3);
    return (
        <div className="ProductDetailsPhotoContainer">
            <div className="ProductDetailsMainPhotoContainer">
                <GatsbyImage
                    key={imagesToShow[0]}
                    image={getImage(imagesToShow[0].file)}
                    alt={name}
                />
            </div>
            <div className="ProductDetailsSecondaryPhotoContainer">
                <div className="ProductDetailsSecondaryPhoto">
                    <GatsbyImage
                            key={imagesToShow[1]}
                            image={getImage(imagesToShow[1].file)}
                            alt={name}
                        />
                </div>
                <div className="ProductDetailsSecondaryPhoto">
                    <GatsbyImage
                        key={imagesToShow[2]}
                        image={getImage(imagesToShow[2].file)}
                        alt={name}
                    />
                </div>
            </div>
        </div>
    );
}

const ProductDetailsInfo = (props) => {
    const {id, etsyLink, name, materialDescription, description, dimensions, priceId, stripeUrl, price} = props;

    const flattenedDimensions = dimensions.reduce((accum, str, idx) => {
        if (idx + 1 === dimensions.length) {
            return accum + str
        }                  
        return `${accum} ${str}, `;
    }, '')

    return (
        <div className="ProductDetailsProductDetails">
            <div className="ProductDetailsPricingDetails">
                <H1>{name}</H1>
                <H2 className="Price">${price} USD</H2>

                {!etsyLink &&<StripeBuyButton priceId={priceId} stripeUrl={stripeUrl} id={id} />}

                {etsyLink &&
                <div className="ProductDetailsPurchaseMethods">
                    <StripeBuyButton priceId={priceId} stripeUrl={stripeUrl} id={id} />
                    or
                    <EtsyLink etsyLink={etsyLink} />
                </div>
                }
            </div>

            <p className="ProductDescription">
                {description}
            </p>

            {dimensions && dimensions.length > 0 &&
                <p>
                    Dimensions: {flattenedDimensions}
                </p>
            }

            <p className="ProductDescription">
                {materialDescription}
            </p>

            <ShippingDisclaimer />

        </div>
    );
}

const ProductDetailsPage = (props) => {
    const { product } = props;
    const {
        coverPhoto,
        dimensions,
        description,
        name,
        category,
    } = product;


    const ldJson = {
        ...baseLdJson,
        "name": name,
        "category": category,
        "image": `https://crochetnest.com${coverPhoto.file.publicURL}`,
        "description": description + '.' + dimensions.join(', '),
    };

    return (
        <>
            <MainSectionWrapper>
                <div className="ProductDetailsPageContainer">
                    <ProductDetailsImages 
                        coverPhoto={coverPhoto} 
                        name={name}
                    />

                    <ProductDetailsInfo
                        {...product}
                    />
                </div>

            </MainSectionWrapper>

            <JsonLd data={ldJson} />
        </>
    )
};

export default ProductDetailsPage;